import { useRouter } from 'next/router'
import Link from 'next/link'
import Navbar from '../../components/Navbar'

export default function Time(){
  const router = useRouter()
  const { goal, experience } = router.query
  const opts = ['20-30','30-45','45-60','60+']
  return (
    <>
      <Navbar />
      <main className="container p-8 max-w-2xl">
        <h2 className="text-2xl font-semibold mb-4">How much time can you train per session?</h2>
        <div className="grid md:grid-cols-2 gap-4">
          {opts.map(o => (
            <Link key={o} href={`/onboard/frequency?goal=${encodeURIComponent(goal)}&experience=${encodeURIComponent(experience)}&time=${encodeURIComponent(o)}`} className="card">
              <h3 className="font-semibold">{o} minutes</h3>
            </Link>
          ))}
      </div>
      </main>
    </>
  )
}
